import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, DoacaoPartidoPolitico } from '../../../../models/dtos/mcg/doacaoPartidoPolitico';
import { FORMA_DOACAO_ENUM_VALUES } from '../../../../models/enumerated/mcg/formaDoacaoEnum';
import { TIPO_BENEFICIARIO_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoBeneficiarioEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface DoacaoPartidoPoliticoEditViewProps extends EditViewProps<DoacaoPartidoPolitico> {}

const DoacaoPartidoPoliticoEditView: FC<DoacaoPartidoPoliticoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<DoacaoPartidoPolitico>({ defaultValues: selectedItem, mode: 'onBlur' });

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const defaultWidth: number = 150;

  useEffect(
    () => {
      const {
        empresaMCG: { id },
      } = globalReducer.globalParameter;

      setTimeout(() => {
        formProps.reset({
          ...selectedItem,
          empresaMCG: {
            id,
          },
        });
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Doações a Campanhas Eleitorais">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresaMCG.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="cnpj"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="CNPJ do Beneficiário"
              placeholder="Informe o CNPJ da Beneficiário"
              type={InputType.CNPJ}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="tipo"
              label="Tipo Beneficiário"
              labelWidth={defaultWidth}
              isPrependLabel={true}
              placeholder="Selecione o tipo Beneficiário"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: TIPO_BENEFICIARIO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="forma"
              label="Forma de Doação"
              isPrependLabel={true}
              placeholder="Selecione a forma de doação"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: FORMA_DOACAO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="valor"
              isPrependLabel={true}
              label="Valor da Doação"
              labelWidth={defaultWidth}
              placeholder="Informe o valor da doação"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default DoacaoPartidoPoliticoEditView;
